.flexComponent {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.05);
  overflow: hidden;
  position: relative;
}

.flexComponent-buttons {
  position: absolute;
  bottom: 10px;
  right: 10px;
  height: 40px;
  width: 100px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}



