.buttonToolTip {
  width: 100px;
  position: absolute;
  top: 110%;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  color: white;
  opacity: 0;
  -webkit-transition: 0.5s ease opacity;
  -o-transition: 0.5s ease opacity;
  transition: 0.5s ease opacity;
}

.buttonToolTip > p {
  margin: 2px;
}



